import React from "react";
import { Button, Modal } from "react-bootstrap";
import { PATH } from "../../../../utils/path";
import { useNavigate } from "react-router-dom";

export default function AddSignatureModal({ isModalOpen }) {
  const navigate = useNavigate();

  return (
    <Modal size="lg" show={isModalOpen} backdrop="static">
      <Modal.Header>
        <Modal.Title style={{ fontSize: "19px", fontWeight: "bold" }}>
          Signature Required
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="p-4 text-center">
        <h5 className="fw-bold">You don't have any signature added</h5>
        <p className="m-0">
          We would love to serve you please proceed by clicking the button below
          and add your signature
        </p>
      </Modal.Body>
      <Modal.Footer className="d-flex justify-content-center">
        <Button
          onClick={() => navigate(`${PATH.PROFILE}?profile=signature`)}
          className="Admin-Add-btn fw-bold"
        >
          Add Signature
        </Button>
      </Modal.Footer>
    </Modal>
  );
}
