import React from "react";
import { Card } from "react-bootstrap";
import Highcharts from "highcharts";
import { HighchartsReact } from "highcharts-react-official";
import { useSelector } from "react-redux";

export default function AppointmentOverview() {
  const { doctorAppointmentCount, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.doctorDashboard
  );
  const aptMalePercent = Math.floor(
    doctorAppointmentCount?.appointmentPreviewMalePercentage
  );
  const aptFemalePercent = Math.floor(
    doctorAppointmentCount?.appointmentPreviewFemalePercentage
  );

  const jobsStatuses = {
    chart: {
      plotBackgroundColor: null,
      plotBorderWidth: null,
      plotShadow: false,
      type: "pie",
      height: 280,
    },
    title: {
      text: " ",
    },
    tooltip: {
      pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>",
    },
    accessibility: {
      point: {
        valueSuffix: "%",
      },
    },
    plotOptions: {
      pie: {
        allowPointSelect: true,
        cursor: "pointer",
        dataLabels: {
          enabled: false,
          style: {
            fontSize: "20px",
          },
        },
        showInLegend: true,
      },
    },
    colors: ["#F26522", "#000071"],
    series: [
      {
        name: "Series",
        colorByPoint: true,
        data: [
          {
            name: `Female ${aptFemalePercent}%`,
            y: aptFemalePercent,
            sliced: true,
            selected: true,
          },
          {
            name: `Male ${aptMalePercent}%`,
            y: aptMalePercent,
          },
        ],
      },
    ],
  };
  return (
    <>
      <Card className="h-100 dr-apt-overview">
        <Card.Body className="p-4">
          <Card.Title className="mb-0">Appointment Overview</Card.Title>
          {isLoading ? (
            <p
              className="my-5 text-center fst-italic"
              style={{
                color: "#999999",
              }}
            >
              Loading...
            </p>
          ) : isSuccess ? (
            <HighchartsReact
              highcharts={Highcharts}
              options={jobsStatuses}
              isPureConfig={true}
            />
          ) : isError ? (
            <p className="my-5 text-center text-danger fst-italic">
              Network Error
            </p>
          ) : null}
        </Card.Body>
      </Card>
    </>
  );
}
