import React, { useEffect, useState } from "react";
import { Card, Row, Col } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import PatientOverview from "./PatientOverview";
import AppointmentOverview from "./AppointmentOverview";
import DoctorDatatable from "./DoctorDatatable";
import Calendar from "./Calendar";
import { useDispatch, useSelector } from "react-redux";
import { getDoctorAptCount } from "../../../app/features/doctorDashboard/doctorDashboard.slice";
import AddSignatureModal from "./AddSignature";

export default function Dashboard() {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const { doctorAppointmentCount, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.doctorDashboard
  );

  const dispatch = useDispatch();

  useEffect(() => {
    if (user?.doctorSignature === false) {
      setIsModalOpen(true);
    }
  }, []);

  useEffect(() => {
    dispatch(getDoctorAptCount());
  }, [dispatch]);

  return (
    <>
      <Row className="main-row">
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Appointments</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {doctorAppointmentCount &&
                      doctorAppointmentCount?.totalAppointments}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.APPOINTMENT_ICON}
                alt="appointment icon"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>New Patients</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {doctorAppointmentCount &&
                      doctorAppointmentCount?.newPatients}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.NEW_PATIENT_ICON}
                alt="new patient icon"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Previous Patients</Card.Title>
                {isLoading ? (
                  <span
                    className="fst-italic"
                    style={{
                      color: "#999999",
                    }}
                  >
                    Loading...
                  </span>
                ) : isSuccess ? (
                  <h3 className="mb-0">
                    {doctorAppointmentCount &&
                      doctorAppointmentCount?.previousPatient}
                  </h3>
                ) : isError ? (
                  <span className="text-danger fst-italic">Network Error</span>
                ) : null}
              </span>
              <img
                src={IMAGES.NEW_PATIENT_ICON}
                alt="new patient icon"
                className="color-dk-blue"
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="my-3">
        <Col xl={8} xs={12}>
          <Row>
            <Col sm={6} xs={12} className="mb-3">
              <PatientOverview />
            </Col>
            <Col sm={6} xs={12} className="mb-3">
              <AppointmentOverview />
            </Col>
            <Col xs={12}>
              <DoctorDatatable />
            </Col>
          </Row>
        </Col>
        <Col xl={4} xs={12}>
          <Row className="h-100">
            <Col xs={12}>
              <Calendar />
            </Col>
          </Row>
        </Col>
      </Row>
      <AddSignatureModal isModalOpen={isModalOpen} />
    </>
  );
}
