import React, { useMemo, useState, useEffect } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { AiFillEye } from "react-icons/ai";
import { Link, useNavigate } from "react-router-dom";
import moment from "moment";

import { getAllAppointedPatients } from "../../../app/features/appointment/appointment.slice";
import { useDispatch, useSelector } from "react-redux";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import { PATH } from "../../../utils/path";
import DataTable from "../../../components/Table/DataTable";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import GenericDatePicker from "../../../components/GenericDatePicker";
import DynamicSelect from "../../../components/DynamicSelect";
import SearchInput from "../../../components/SearchInput";

const options = [
  { value: null, label: "Gender" },
  { value: 101, label: "Male" },
  { value: 102, label: "Female" },
  { value: 103, label: "Other" },
];

export default function DoctorDatatable() {
  const [genderValue, setGenderValue] = useState(null);
  const [selectedDate, setSelectedDate] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);
  const pageSize = useMemo(() => 3, []);
  const [searchValue, handleSearch] = useDebouncedSearch(500);

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { allAppointedPatient, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.appointment
  );
  const { user } = useSelector((state) => state.auth);

  useEffect(() => {
    const finalData = {
      pageNo: pageNumber,
      size: pageSize,
      isPagination: true,
      doctorId: user?.userId,
      search: searchValue,
      gender: genderValue ? +genderValue : null,
      dob: selectedDate ? moment(selectedDate).format("YYYY-MM-DD") : null,
    };
    dispatch(getAllAppointedPatients(finalData));
  }, [
    dispatch,
    searchValue,
    user?.userId,
    genderValue,
    selectedDate,
    pageNumber,
  ]);

  const columns = [
    {
      dataField: "patientId",
      text: "ID",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "patientName",
      text: "Patient Name",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, cell) => {
        return (
          <div className="d-flex align-items-center">
            <img
              src={
                cell?.imageUrl
                  ? cell?.imageUrl
                  : `https://ui-avatars.com/api/?name=${`${cell?.patientName}`}&background=000071&color=fff`
              }
              alt="apt patient"
              className="me-2 dt-round-img"
            />
            <span style={{ lineHeight: "1.2" }}>
              <p className="m-0 table-bold-text">{cell?.patientName}</p>
              <p className="m-0 table-normal-text">{cell?.email}</p>
              <p className="m-0 table-normal-text" style={{ color: "#999999" }}>
                {cell?.phoneNumber}
              </p>
            </span>
          </div>
        );
      },
    },
    {
      dataField: "currentAddress",
      text: "Location",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        return cell || "N/A";
      }, 
    },
    {
      dataField: "gender",
      text: "Gender",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row) => (row ? row : "N/A"),
    },
    {
      dataField: "dob",
      text: "DOB",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        return cell ? moment(cell).format("DD/MM/YYYY") : "N/A";
      }, 
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, cell) => (
        <Link
          to={PATH.PATIENTS_DETAILS.replace(":patientId", cell?.patientId)}
          className="table-action"
        >
          <AiFillEye /> View{" "}
        </Link>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };

  return (
    <>
      <Card className="mb-0 dr-date-wrapper patient-dashboard-table">
        <Card.Body className="p-0">
          <Row className="px-4 pt-3">
            <Col md={4}>
            <SearchInput onChange={handleSearch} className="me-2 mb-3"/>
            </Col>
            <Col
              md={8}
              className="d-flex flex-wrap align-self-center justify-content-end"
            >
              <DynamicSelect
                label="Gender"
                onChange={(e) => setGenderValue(e.target.value)}
                options={options}
              />
              <GenericDatePicker
                selectedDateState={selectedDate}
                onChange={(date) => setSelectedDate(date)}
                className="me-2"
              />
              <Button
                variant="outline-secondary"
                className="custom-outline-btn mb-3"
                onClick={() => navigate(PATH.PATIENTS)}
              >
                View All
              </Button>
            </Col>
          </Row>

          {isLoading ? (
            <FullScreenLoader fullHeight={true} />
          ) : isSuccess ? (
            <span className="doctor-datatable">
              <DataTable
                tableColumns={columns}
                tableData={
                  allAppointedPatient?.data ? allAppointedPatient?.data : []
                }
                keyField="patientId"
                pageNumber={pageNumber}
                totalRecords={allAppointedPatient?.totalCounts}
                dataPerPage={pageSize}
                handlePageChange={handlePageChange}
              />
            </span>
          ) : isError ? (
            <p className="my-5 text-center text-danger fst-italic">
              Network Error
            </p>
          ) : null}
        </Card.Body>
      </Card>
    </>
  );
}
