import React from "react";

export default function DynamicSelect({
  className = "me-2",
  label,
  options,
  onChange,
}) {
  return (
    <div className={`${className}`}>
      <select
        onChange={onChange}
        className="form-select pe-5"
        aria-label={`Select ${label}`}
      >
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
    </div>
  );
}
