import React, { useEffect, useState } from "react";
import { Button, Card } from "react-bootstrap";
import DatePicker from "react-datepicker";
import IMAGES from "../../../assets/images";
import { AiFillClockCircle } from "react-icons/ai";
import { useDispatch, useSelector } from "react-redux";
import { getAllAppointment } from "../../../app/features/appointment/appointment.slice";
import moment from "moment";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../utils/path";

export default function Calendar() {
  const [startDate, setStartDate] = useState(null);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { allAppointment, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.appointment
  );

  function doctorTeleConsultation(data) {
    localStorage.setItem("slotDescription", JSON.stringify(data));
    navigate(PATH.TELE_CONSULTATION);
  }

  useEffect(() => {
    const finalData = {
      doctorId: user?.userId,
      isDashbaordAppointment: true,
      startdate: startDate
        ? moment(startDate).format("YYYY-MM-DD")
        : moment(new Date()).format("YYYY-MM-DD"),
      endDate: startDate
        ? moment(startDate).format("YYYY-MM-DD")
        : moment(new Date()).format("YYYY-MM-DD"),
      statusId: 202,
    };
    dispatch(getAllAppointment(finalData));
  }, [dispatch, user?.userId, startDate]);

  return (
    <>
      <Card className="py-4 m-0 h-100">
        <Card.Body className="p-0">
          <span className="d-flex align-items-center px-4 pb-0 ">
            <img
              src={IMAGES.APPOINTMENT_ICON}
              alt="new patient icon"
              style={{ width: "20px" }}
              className="me-2 color-dk-blue"
            />
            <Card.Title className="m-0">Calendar</Card.Title>
          </span>
          <Card.Body className="px-0">
            <span className="patient-calendar">
              <DatePicker
                selected={startDate}
                onChange={(date) => setStartDate(date)}
                startDate={startDate}
                inline
              />
            </span>
          </Card.Body>
          <div className="px-3" style={{ height: "22rem" }}>
            <p style={{ fontSize: "20px", fontWeight: 500 }}>
              Upcoming Appointment
            </p>
            <div
              className="h-100 no-margin-bottom-last"
              style={{ maxHeight: "19rem", overflowY: "scroll" }}
            >
              {isLoading ? (
                <span
                  className="fst-italic h-100 d-flex justify-content-center align-items-center flex-column"
                  style={{
                    color: "#999999",
                  }}
                >
                  Loading...
                </span>
              ) : isSuccess ? (
                allAppointment?.data?.length > 0 ? (
                  allAppointment?.data?.map((item, index) => (
                    <Card.Body className="mb-3 calendar-card">
                      <Card.Title style={{ fontSize: "24px" }}>
                        {item?.reasonForAppoinment}
                      </Card.Title>
                      <div className="d-flex align-items-center">
                        <span className="me-3 apt-inner-card p-2 d-flex align-items-center flex-column">
                          <p
                            style={{ fontSize: "26px", fontWeight: 600 }}
                            className="mb-0"
                          >
                            {moment(item?.appointmentDate).format("MMM DD")}
                          </p>
                          <p className="m-0 table-normal-text">
                            <AiFillClockCircle
                              className="me-2"
                              style={{ color: "#999999" }}
                            />
                            {item?.appointmentStartTime}
                          </p>
                        </span>
                        <span style={{ lineHeight: "1.4" }}>
                          <p className="m-0" style={{ fontSize: "18px" }}>
                            <span className="font-weight-600">Patient:</span>{" "}
                            {item?.patientName}
                          </p>
                          <p className="m-0" style={{ fontSize: "18px" }}>
                            <span className="font-weight-600">Doctor:</span>{" "}
                            {item?.doctorName}
                          </p>
                        </span>
                      </div>
                      <Button
                        key={item?.appointmentId}
                        className="mt-4 w-100"
                        style={{
                          backgroundColor: "#000071",
                          border: "0px",
                        }}
                        onClick={() => doctorTeleConsultation(item)}
                      >
                        Join Appointment
                      </Button>
                    </Card.Body>
                  ))
                ) : (
                  <div className="h-100 py-2 d-flex justify-content-center align-items-center flex-column">
                    <img
                      src={IMAGES.UPCOMING_APPOINTMENT_ICON}
                      alt="upcoming appointment"
                    />
                    <p
                      className="mb-0 mt-2 color-99 text-center"
                      style={{
                        fontSize: "20px",
                        fontWeight: 400,
                        width: "54%",
                      }}
                    >
                      No appointment scheduled
                    </p>
                  </div>
                )
              ) : isError ? (
                <span className="text-danger fst-italic h-100 d-flex justify-content-center align-items-center flex-column">
                  Network Error
                </span>
              ) : null}
            </div>
          </div>
        </Card.Body>
      </Card>
    </>
  );
}
