import React from "react";
import { Card } from "react-bootstrap";
import ReactApexChart from "react-apexcharts";
import { useSelector } from "react-redux";

export default function PatientOverview() {
  const { doctorAppointmentCount, isLoading, isSuccess, isError } = useSelector(
    (state) => state?.doctorDashboard
  );
  const patientOverview = {
    series: [
      doctorAppointmentCount?.patientsPreviewFemalePercentage === undefined
        ? []
        : doctorAppointmentCount?.patientsPreviewFemalePercentage,
      doctorAppointmentCount?.patientsPreviewMalePercentage === undefined
        ? []
        : doctorAppointmentCount?.patientsPreviewMalePercentage,
    ],
    options: {
      chart: {
        type: "donut",
      },
      plotOptions: {
        pie: {
          donut: {
            labels: {
              show: true,
              position: "bottom",
              value: {
                show: true,
                fontSize: "33px",
                fontWeight: 500,
                color: undefined,
              },
              total: {
                show: true,
                showAlways: true,
                label: "Total Patients",
                fontSize: "16px",
                fontWeight: 500,
                color: "#999999",
                formatter: function (w) {
                  return doctorAppointmentCount?.totalPatientCount || 0;
                },
              },
            },
          },
        },
      },
      labels: ["Female", "Male"],
      colors: ["#F26522", "#000071"],
      dataLabels: {
        enabled: false,
      },
      legend: {
        position: "bottom",
        fontSize: "16px",
        fontWeight: "bold",
        formatter: function (val, opts) {
          return val + " " + opts.w.globals.series[opts.seriesIndex] + "" + "%";
        },
      },
      title: {
        enabled: false,
      },
      tooltip: {
        shared: false,
        y: {
          formatter: function (val) {
            return val + "%";
          },
        },
      },
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 200,
            },
            legend: {
              position: "bottom",
            },
          },
        },
      ],
    },
  };
  return (
    <>
      <Card className="h-100">
        <Card.Body className="p-4">
          <Card.Title className="mb-0">Patient Overview</Card.Title>
          {isLoading ? (
            <p
              className="my-5 text-center fst-italic"
              style={{
                color: "#999999",
              }}
            >
              Loading...
            </p>
          ) : isSuccess ? (
            <ReactApexChart
              options={patientOverview.options}
              series={patientOverview.series}
              type="donut"
              height={280}
              className="p-3"
            />
          ) : isError ? (
            <p className="my-5 text-center text-danger fst-italic">
              Network Error
            </p>
          ) : null}
        </Card.Body>
      </Card>
    </>
  );
}
